import React from 'react'
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { remove } from '../store/cartSlice';


const Cart = () => {

const dispatch=useDispatch();
const products = useSelector(state=>state.cart);
console.log(products);
const handleRemove = (productId) => {
  dispatch(remove(productId));
};

  return (
    <div>
      <h4 className='heading text-center'>Cart</h4>
      {
         products.length == 0 ?
         <div><h4 className="text-center">No product in cart.</h4></div>:
         <div>
         <table className='table'>
        <thead>
          <tr>
            <th>No.</th>
            <th>Product Image</th>
            <th>Product Title</th>
            <th>Product Price</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {
            products.map((product,index) =>(
              <tr key={index}>
             <td data-label="No.">{index+1}</td>
             <td data-label="Product Image"><img src={product.image} style={{width:'12rem', height: '14rem' }} alt={product.title} className="cart_image"/></td>
             <td data-label="Product Title">{product.title}</td>
             <td data-label="Product Price">{product.price}</td>
             <td data-label="Action"><Button variant="primary" onClick={()=>handleRemove(product.id)}>Remove</Button></td>
           </tr>
            )
            )  

          }
         
          </tbody>
          </table>
          </div>
      }
     </div> 
  )
}

export default Cart

