import React, { useEffect} from 'react'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import { useDispatch } from 'react-redux';
import {add} from "../store/cartSlice";
import { fetchProducts } from '../store/productSlice';
import { useSelector } from 'react-redux';
import { STATUSES } from '../store/productSlice';
import loading from '../spinner.gif';


const Products = () => {
 //  const [products, setProducts] = useState([]);

    const dispatch=useDispatch();

    const {data:products,status}=useSelector((state)=>state.product);
  
    useEffect(() => {
        dispatch(fetchProducts());
        /*    const fetchProducts = async () => {
            const res = await fetch('https://fakestoreapi.com/products');
            const data = await res.json();
            console.log(data);
            setProducts(data);
        };
        fetchProducts();*/
    }, []);

    const handleAdd=(product)=>{
        dispatch(add(product));
    }

    if(status===STATUSES.LOADING)
    {
        //return <h2>Loading...</h2>
        return <div className='text-center'><img src={loading} alt="Loading"  style={{width:60,height:60}}/></div>
    }

    if(status===STATUSES.ERROR)
    {
        return<h2>Something went wrong</h2>
    }

    

    return (
        <div className='container mt-3'>
            <div className='row d-flex justify-content-center align-items-center'>
                {
                   
                    products.map((product,index) => (
                        <Card style={{width:'18rem'}} key={index} className="mx-2 mt-4 card_style card_text">
                            <Card.Img variant="top" src={product.image}  style={{width:'12rem', height: '14rem' }} className="mt-3 card_image" />
                            <Card.Body>
                                <Card.Title style={{fontSize:18}}>{product.title}</Card.Title>
                                <Card.Text>
                                {product.price}
                                </Card.Text>
                                <Button variant="primary" onClick={()=>handleAdd(product)}>Add to Cart</Button>
                            </Card.Body>
                        </Card>

                    ))

                }
            </div></div>
    )
}



export default Products