import React from "react";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

const Header =()=>{

   const items = useSelector((state)=>state.cart); 
   return (
    <div className='main_header'>
    <Navbar expand="lg" variant="light" >
        <Container>
        <Navbar.Brand href="#home"  className="newFont text-decoration-none mx-3 mt-3">E Cart</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto mx-5">
          <NavLink className="navLink text-decoration-none navFont align_home" to="/">Home</NavLink>
        <NavLink className="cartSpacing navFont align_cart" to="/cart">Cart</NavLink>
        </Nav>
        <span className="cartCount navFont">Cart Items : {items.length}</span>
        </Navbar.Collapse>
        </Container>
      </Navbar>
      </div>
   );
}

export default Header;